@import './Global.scss';
$progressWidth: 25px;

.side-bar{
  @include breakpoint("desktop"){
    border-left: 1px $secondaryColor solid;
  }
}
.skill-name{
  display: block;
  padding-left: 20px;
}
.greet{
  text-align: left;
  margin:50px 30px;
  &_subtitle{
    margin-bottom:0.6em;
  }
  div{
    margin-bottom: 30px;
  }
}
.profile{
  position: relative;
   h2{
     display: block;
     margin-top:50px;
   }
}
.skill{
  margin:80px 0;
  text-align: center;
  position: relative;
}
.section-title{
  position: relative;
  &::before{
    position:absolute;
    content:" ";
    display: block;
    margin-top:50px;
    padding-right:30px;
    display: inline-block;
    height:5px;
    width:40px;
    margin-left: 50px;
    border-radius: 50px;
    background: $themeTwo;
    transition: width 0.4 ease-in 0.8;
}
&::before{
  width: 80px;
}
}
.skill-box{
  padding: 10px;
  margin:10px 20px;
  border:1px solid $secondaryColor;
  border-radius: 10px;
  text-align: center;
  h5{
    color:$themeThree;
    font-weight: 600;
  }
  li{
    margin:3px;
  }
  p{
    font-weight: 400;;
  }
}
.companies{
  display: flex;
  flex:1 1 100%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  img{
    min-width: 150px;
    max-width: 280px;
    margin:1em 2em;
  }
}